body {
	height: 100%;
	width: 100%;
	background-color: #3a4042;
	color: white;
	font-family: "Josefin Sans", sans-serif;
	font-size: 10px;
	text-transform: uppercase;
	font-weight: 200;
	letter-spacing: 1.5px;
	z-index: 1;
}

h1 {
	font-size: 16px;
	margin-left: 5%;
}

h2 {
	font-size: 14px;
	margin-left: 5%;
}

h3 {
	font-size: 12px;
	margin-left: 5%;
}

h4 {
	font-size: 12px;
	margin-left: 5%;
}

h5 {
	font-size: 10px;
	margin-left: 5%;
}

p {
	font-size: 12px;
	margin-left: 5%;
}

a {
	color: white;
}

.text-purple {
	color: #1c1028;
}

.text-green {
	color: #53827f;
}

.text-white {
	color: white;
}

.navbar {
	background-color: #1c1028;
	display: flex;
	flex-direction: column;
	width: 300px;
	height: 1600px;
	margin-right: 0px;
	margin-top: -25%;
}

.maincontainer {
	display: flex;
	flex-direction: row;
	padding-top: 5%;
}

.maintext {
	margin-top: 0;
	margin-left: 5%;
	font-size: 14px;
	border: none;
	width: 900px;
}

.flex-row {
	display: flex;
	flex-direction: row;
	width: 140%;
	align-items: middle;
}

.flex-row-3 {
	display: flex;
	flex-direction: row;
	width: 140%;
}

.flex-row-3-child {
	width: 25%;
	height: auto;
}

input {
	margin-bottom: 3%;
	margin-left: 5%;
	width: 70%;
	font-size: 14px;
}

textarea {
	margin-bottom: 3%;
	margin-left: 5%;
}

form {
	margin-left: 5%;
	margin-right: 5%;
	width: 90%;
}

.form-input {
	background-color: #3a4042;
	border: none;
	color: white;
	width: 95%;
	border-bottom: white solid 1px;
	margin-bottom: 10%;
}

.right-bottom {
	right: 10%;
}

#footer {
	display: inline-block;
	align-content: center;
	margin-left: 40%;
}

.btn {
	margin: 20px;
	font-size: 12px;
	padding-top: 15px;
	width: 180px;
	height: 45px;
	outline: none;
	cursor: pointer;
	transition: all .4s;
	border: none;
}

.btn:hover {
	font-size: 14px;
	color: white;
	vertical-align: baseline;
}

.btn-purple {
	color: white;
	background-color: #1c1028;
}

.btn-green {
	color: white;
	background-color: #53827f;
}

.btn-green:hover {
	background-color: #1c1028;
}

.btn-salmon {
	color: white;
	background-color: #ff6666;
}

.btn-salmon:hover {
	background-color: #1c1028;
}

.btn-card {
	margin: 20px;
	font-size: 12px;
	padding-top: 15px;
	width: 260px;
	height: 45px;
	outline: none;
	cursor: pointer;
	transition: all .4s;
	border: none;
}

.btn-card:hover {
	font-size: 14px;
	color: white;
	vertical-align: baseline;
}

#btn-login {
	margin-left: 25%;
}

.logo {
	width: 50%;
	height: auto;
	margin-left: 25%;
}

.acess1 {
	display: inline-block;
	flex-direction: row;
	padding-left: 30px;
	align-items: center;
	margin-left: 25%;
	margin-bottom: 5%;
	margin-top: -15%;
}

.entire-screen {
	background-image: url("../images/people.jpeg");
	background-size: cover;
	background-ex: -1;
	overflow: visible;
}

.card {
	background-color: #53827f;
	color: white;
	width: 350px;
}

#profile_img {
	margin-right: 5%;
	margin-left: 5%;
	border-radius: 20%;
	margin-top: -8%;
}

.Chatbox {
	position: fixed;
	right: 10px;
	top: 5%;
	width: 350px;
	border: 1px solid #ccc;
}

.map {
	margin-left: 0%;
}

.infowindow {
	color: #1c1028;
	font-size: 16px;
}

.iconMarker {
	height: 20px;
	color: #1c1028;
}

.card {
	height: auto;
	width: 300px;
	margin: 5%;
	font-size: 14px;
}

.card-title {
	margin-top: 5%;
	color: #1c1028;
}

.card-text {
	margin: 3%;
	padding-bottom: 5%;
}

.card-image {
	background-image: url("../images/default_project.jpg");
	background-size: cover;
}

.chat-button {
	position: absolute;
	right: 10px;
	top: 10px;
	background: #ff6666;
	height: 50px;
	font-size: 16px;
	color: white;
	padding: 10px;
	cursor: pointer;
}

.form-container {
	top: 10px;
	right: 10px;
	height: 400px;
	border: 1px solid #1c1028;
}

.form-container h1 {
	padding: 10px;
	background-color: #1c1028;
	font-size: 20px;
	color: white;
	margin: 0;
	margin-bottom: 10px;
}

.form {
	padding: 30px;
}

.ChatApp {
	position: fixed;
	right: 10px;
	top: 10px;
	width: 350px;
	border: 1px;
	background-color: #53827f;
}

.form {
	padding: 30px;
}

.input {
	display: block;
	width: 90%;
	margin-left: 5%;
	margin-right: 5%;
	height: 50px;
	font-size: 16px;
	margin-left: 5%;
	height: 50px;
	font-size: 20px;
	padding-left: 10px;
}

.submit {
	height: 40px;
	padding: 0 20px;
	background-color: #ff6666;
	color: white;
	border-radius: 5px;
	font-size: 16px;
	cursor: pointer;
}

.chatbox {
	margin: 0 auto;
	height: 80vh;
	border: 1px solid;
}

.header {
	padding: 20px;
	margin-left: 0%;
	color: white;
	margin-top: 0;
	text-align: left;
	border-bottom: 1px solid #1c1028;
	background-color: #1c1028;
}

.message-list {
	padding: 0px 20px;
	overflow: scroll;
	max-height: calc(100vh - 150px);
	margin-bottom: 35px;
}

.message-sender {
	font-weight: bold;
	font-size: 1em;
	margin: 0;
}

.message-text {
	margin-bottom: 20px;
	margin-top: 5px;
}

.message-input {
	width: 70%;
	height: 50px;
	font-size: 16px;
	padding-left: 40px;
}

.message-submit {
	width: 30%;
	background-color: #1c1028;
	color: white;
	font-size: 16px;
	cursor: pointer;
}

.input-field {
	position: absolute;
	width: 100%;
	left: 0;
	margin-left: 0%;
	background-color: #53827f;
}

.input-send {
	bottom: 0;
	width: 80%;
	left: 5%;
}

.login {
	background-color: #3a4042;
	margin-left: 35%;
	margin-top: 8%;
	width: 400px;
	height: 500px;
	border: solid 1px white;
	padding-bottom: 50px;
}

.icon {
	margin-left: 25%;
	height: 200px;
}

.row {
	background-color: #3a4042;
	border: none;
	color: white;
	width: 300px;
	margin-left: 5%;
	margin-right: 5%;
	align-items: baseline;
}

.login-input {
	background-color: #3a4042;
	border: none;
	color: white;
	width: 95%;
	border-bottom: white solid 1px;
	margin-bottom: 10%;
}

.signup {
	background-color: #3a4042;
	margin-left: 5%;
	margin-right: 5%;
	margin-top: 2%;
	border: solid 1px white;
	padding: 20px;
	padding-bottom: 50px;
}

.signup-input {
	background-color: #3a4042;
	border: none;
	color: white;
	width: 80%;
	border-bottom: white solid 1px;
	margin-bottom: 5%;
	margin-left: 5%;
}
